.home .setflag {
  border: 1px solid #ebedf0;
  background-color: #fff;
  padding-bottom: 40px;
  box-sizing: border-box;
  padding: 70px 25px 40px;
  position: relative;
  display: flex;
}
.home .setflag .setflag-left {
  flex: 3;
}
.home .setflag .setflag-left .setflag-flag {
  flex: 1;
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.home .setflag .setflag-left .setflag-flag > div {
  margin-top: 15px;
}
.home .setflag .setflag-left .another {
  text-align: right;
  margin-right: 40px;
  font-size: 16px;
  color: #000;
  margin-bottom: 10px;
}
.home .setflag .setflag-left .another > div {
  display: inline-block;
  cursor: pointer;
}
.home .setflag .setflag-left .another > div span {
  margin-left: 10px;
}
.home .setflag .setflag-left .setflag-add {
  margin-top: 40px;
}
.home .setflag .setflag-right {
  flex: 2;
}
.home .setflag .setflag-right h3 {
  font-size: 22px;
  text-align: center;
}
.home .setflag .setflag-right .setflag-task-progress {
  margin-top: 15px;
  width: 90%;
  margin-left: 25px;
}
.home .setflag .setflag-right .setflag-task {
  box-sizing: border-box;
  padding: 15px 30px;
  max-height: 600px;
  overflow: auto;
}
.home .setflag .setflag-right .setflag-task .setflag-task-title {
  margin-top: 25px;
  width: 100%;
  transform: scale(0.95);
}
.home .setflag .setflag-right .setflag-task .setflag-task-title .task-header {
  display: flex;
  align-items: center;
}
.home .setflag .setflag-right .setflag-task .setflag-task-title .task-main {
  display: flex;
  align-items: center;
  margin-top: 15px;
}
.home .setflag .setflag-right .setflag-task .setflag-task-title .task-main span {
  line-height: 20px;
  font-size: 15px;
}
.home .setflag .setflag-right .setflag-task .setflag-task-title .task-main .setflag-task-btn {
  transition: all 0.3s linear;
  background-color: #52c41a;
  border-color: #52c41a;
}
.home .setflag .setflag-right .setflag-task::-webkit-scrollbar {
  width: 4px;
}
.home .setflag .setflag-right .setflag-task::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}
.home .setflag .setflag-right .setflag-task::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 0;
  background: rgba(0, 0, 0, 0.1);
}
.daily-punch-wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.daily-punch-wrapper .daily-punch-title {
  outline: none;
  border: none;
  transition: all 0.1s linear;
  width: 400px;
  height: 50px;
  font-size: 20px;
}
.daily-punch-title:focus {
  border-bottom: 1px solid #000;
}
